export default {
	data() {
		return {
			title: '计量单位',
			datas: null,
			searchDatas: null,
			searchKey: '',
			showAddModal: false,
			showEditModal: false,
			addUnitName: '',
			editUnitName: ''
		}
	},
	onLoad(options) {
	},
	onShow() {
		this.datas = uni.getStorageSync('productUnit')
		this.searchDatas = this.datas
	},
	methods: {
		handleShowAddModal() {
			this.addUnitName = ''
			this.$nextTick(function(){
				this.showAddModal = true
			})
		},
		handleReturnItem(val) {
			let pages =  getCurrentPages()
			let prevPage = pages[pages.length - 2]
			prevPage.setData({
				selectUnit: val
			})
			uni.navigateBack({
				delta: 1
			})
		},
		handleShowEditModal(val) {
			this.editUnitName = val.name
			this.$nextTick(function(){
				this.showEditModal = true
			})
		},
		handleHideAddModal() {
			this.showAddModal = false
		},
		handleHideEditModal() {
			this.showEditModal = false
		},
		handleAdd() {
			if (this.addUnitName) {
				this.datas.push({ name: this.addUnitName })
				uni.setStorageSync('productUnit', this.datas)
				this.searchDatas = this.datas
				this.addUnitName = ''
			}
			this.$nextTick(function(){
				this.showAddModal = false
			})
		},
		handleEdit(val) {				
			if (this.editUnitName) {
				for (let item of this.datas) {
					if (item.name == val.name) {
						item.name = this.editUnitName
					}
				}
				uni.setStorageSync('productUnit', this.datas)
				this.searchDatas = this.datas
				this.editUnitName = ''
			}
			this.$nextTick(function(){
				this.showEditModal = false
			})
		},
		handleCancelSearch() {
			this.searchKey = ''
		}
	},
	watch: {
		searchKey(val) {
			if (val) {
				this.searchDatas = this.datas.filter((item) => {
					return item.name.indexOf(val) !== -1
				})
			} else {
				this.searchDatas = this.datas
			}
		}
	}
}
